@import "../../theme.scss";

.playlistdetails {
  height: 100%;
  position: relative;

  .playlist-header {
    padding: 10px 15px 0 15px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    border: 1px solid rgba(235, 235, 235, 1);

    .p-details-information {
      height: 61px;
      display: flex;

      button {
        align-self: center;
        height: 42px;
        width: 150px;

        i {
          margin-left: 5px;
        }
      }

      .choose-frame {
        align-self: center;
        width: 150px;

        span {
          display: block;
          height: 42px;
          border-radius: 4px;
          color: #fff;
          background-color: $bg-color;
          line-height: 42px;
          text-align: center;
          cursor: pointer;
        }
      }

      .file-num,
      .frame-num {
        align-self: center;
        cursor: pointer;
        border: 1px solid #afafaf;
        border-top-right-radius: 8px;
        border-top-left-radius: 8px;
        border-bottom: 0;
        padding: 5px;

        &:hover {
          border-color: $bg-color;
        }
      }

      .file-num,
      .frame-num,
      .creation-time,
      .last-update {
        margin: 0 13px;

        .p-d-i-icon {
          height: 100%;
          display: inline-block;
          font-size: 32px;
          vertical-align: middle;
          margin-right: 10px;
          padding-top: 4px;

          i {
            font-size: 32px;
          }
        }

        .creation-time-icon {
          display: block;
          width: 40px;
          height: 40px;
          background: url('../../images/icon2.png');
        }

        .last-update-icon {
          display: block;
          width: 40px;
          height: 40px;
          background: url('../../images/icon3.png');
        }

        .num-text {
          float: right;
          height: 100%;
          padding: 0 3px;
          text-align: left;

          .text {
            margin: 0;
            height: 24px;
            font-size: 13px;
            font-weight: 400;
            color: rgba(1, 1, 1, 0.6);
            line-height: 24px;
          }

          .num {
            margin: 0;
            height: 24px;
            font-size: 16px;
            font-weight: 400;
            color: rgba(25, 25, 25, 1);
            line-height: 24px;
          }
        }
      }

      .befort {
        position: relative;
        color: $bg-color;
        border-color: $bg-color;

        &::after {
          display: block;
          content: "";
          position: absolute;
          bottom: -1px;
          left: -5px;
          right: -5px;
          height: 2px;
          background-color: $bg-color;
          z-index: 1;
        }

        .text,
        .num {
          color: $bg-color !important;
        }
      }

      .playlist-set {
        flex: 1;

        .playlist-msg-icon {
          float: right;
          width: 49px;
          height: 49px;
          background: $bg-color;
          border-radius: 4px;
          text-align: center;
          cursor: pointer;

          i {
            color: #fff;
            font-size: 24px;
            padding-top: 2px;
            line-height: 49px;
          }
        }

        .playlist-msg-icon {
          margin-right: 15px;
        }
      }

      .Time {
        align-self: center;

        .creation-time,
        .last-update {
          display: inline-block;
        }
      }
    }
  }

  .playlistdetails-boby {
    width: 100%;
    position: absolute;
    top: 110px;
    bottom: 0;
  }

  .playlistdetails-main {
    height: 100%;
    position: relative;

    .playlist-operation {
      position: relative;
      display: flex;
      height: 45px;
      background: rgba(255, 255, 255, 1);
      border-radius: 3px;
      border: 1px solid rgba(235, 235, 235, 1);
      padding: 0 15px;

      .checkAll {
        align-self: center;
        display: flex;

        i {
          margin-right: 5px;
          cursor: pointer;
          font-size: 20px;
          border-radius: 4px;
        }

        .checkAll-text,
        .invert-Selection {
          font-size: 14px;
          color: rgba(2, 2, 2, 1);
          line-height: 20px;
        }

        .checkAll-text {
          margin-right: 10px;
        }

        .invert-Selection {
          cursor: pointer;
          color: $bg-color;
        }
      }

      .playlist-delete {
        align-self: center;
        padding-left: 24px;
        border-left: 1px solid #e9e9e9;
        margin-left: 24px;

        span {
          display: block;
          height: 30px;
          padding: 0 10px;
          border-radius: 3px;
          border: 1px solid rgba(191, 191, 191, 1);
          font-size: 12px;
          font-weight: 400;
          color: rgba(31, 31, 31, 1);
          line-height: 30px;
          cursor: pointer;

          i {
            margin-right: 10px;
          }
        }
      }
    }

    .playlistdetails-main-left {
      height: 100%;

      .imglist-inner {
        width: 100%;
        position: absolute;
        top: 45px;
        bottom: 0;
        left: 0;
        right: 340px;
      }
    }

    .toframes {
      width: 100%;
      height: 100%;
    }

    .playlistdetails-main-right {
      height: 100%;
      position: relative;

      .bind-frameslist {
        width: 100%;
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        align-content: flex-start;
        overflow: auto;

        .boxlist {
          display: flex;
          padding: 5px 10px;
        }

        .list {
          display: flex;
          flex-direction: column;
          position: relative;
          height: 100%;
          width: 100%;
          background: rgba(255, 255, 255, 1);
          box-shadow: 0px 2px 6px 0px #ebebeb;
          border: 2px solid rgba(255, 255, 255, 1);
          border-radius: 4px;

          .rahmen-cover {
            height: 75%;
            display: flex;
            align-items: center;
            justify-content: center;
            border-bottom: 1px solid #e4e4e4;

            .cover-img {
              position: relative;
              width: 146px;
              height: 198px;
              box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.3);
              padding: 10px 8px;
              overflow: hidden;

              .back-img {
                display: block;
                height: 100%;
                width: 100%;
                background: url("../../images/framelogo.png") no-repeat;
                background-position: center;
              }
            }
          }

          .rahmen-name-series {
            height: 25%;
            padding-left: 14px;
            padding-right: 14px;
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            .name_info {
              height: 21px;
              line-height: 21px;

              .name {
                display: inline-block;
                width: 160px;
                font-size: 16px;
                color: rgba(0, 0, 0, 1);
                line-height: 21px;
              }

              .admin {
                float: right;
                background-color: $bg-color;
                color: #fff;
                font-size: 14px;
                border-radius: 3px;
                padding: 0 5px;
                line-height: 21px;
              }
            }

            .series {
              height: 19px;
              font-size: 14px;
              color: rgba(179, 180, 183, 1);
              line-height: 19px;
              margin-top: 9px;
              margin-bottom: 10px;
            }
          }
        }

        .frame-ringht {
          position: absolute;
          right: 15px;
          bottom: 10px;

          span {
            position: absolute;
            display: block;
            width: 48px;
            height: 24px;
            text-align: center;
            background: $bg-color;
            border-radius: 3px;
            font-size: 12px;
            color: rgba(255, 255, 255, 1);
            line-height: 24px;
            top: 0;
          }

          i {
            color: $bg-color;
            font-size: 28px;
            cursor: pointer;
          }
        }
      }
    }
  }

  .framecheckAll {
    border-bottom: 1px solid #e9e9e9;
    height: 25px;
    display: flex;
    align-items: center;
    padding-left: 10px;

    i {
      margin-right: 10px;
    }
  }

  .frame_list {
    height: 60vh;
    overflow-y: auto;

    .choose-framer-list {
      float: left;
      position: relative;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 2px 6px 0px #ebebeb;
      border: 2px solid rgba(255, 255, 255, 1);
      border-radius: 4px;
      margin: 5px 10px;
      cursor: pointer;

      .choose-framerlist-check {
        position: absolute;
        display: block;
        top: 5px;
        right: 5px;
        width: 20px;
        height: 20px;
        background: rgba(255, 255, 255, 1);
        border-radius: 3px;
        border: 2px solid rgba(175, 175, 175, 1);

        i {
          vertical-align: top;
          color: #fff;
          visibility: hidden;
        }
      }

      .default {
        position: absolute;
        display: block;
        top: 5px;
        right: 5px;
        color: #fff;
        background-color: $bg-color;
        border-radius: 3px;
        padding: 0 5px;
        z-index: 99;
      }

      .choose-framer-cover {
        padding: 10px 30px;
        border-bottom: 1px solid #e4e4e4;

        .cover-img {
          position: relative;
          width: 146px;
          height: 198px;
          box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.3);
          padding: 10px 8px;
          overflow: hidden;

          .back-img {
            display: block;
            height: 100%;
            width: 100%;
            background: url("../../images/framelogo.png") no-repeat;
            background-position: center;
          }
        }
      }

      .choose-framer-name-series {
        padding: 5px 14px;

        p {
          margin: 0;
        }

        .name {
          .frame-name {
            float: left;
            font-size: 16px;
            color: rgba(0, 0, 0, 1);
            line-height: 21px;
            width: 80px;
          }

          .admin_default {
            float: right;

            .admin {
              margin-right: 5px;
            }

            .admin {
              background-color: $bg-color;
              color: #fff;
              font-size: 14px;
              border-radius: 3px;
              padding: 0 3px;
            }
          }
        }

        .series {
          height: 19px;
          font-size: 14px;
          color: rgba(179, 180, 183, 1);
          line-height: 19px;
          margin-top: 5px;
        }
      }
    }

    .choose-framer-list-check {
      border: 2px solid $bg-color;

      .choose-framerlist-check {
        background-color: $bg-color;
        border: 2px solid $bg-color;

        i {
          visibility: initial;
        }
      }
    }

    .nodata {
      display: block;
      width: 100%;
      text-align: center;
      padding-top: 20px;
      font-size: 22px;
    }
  }

  .list-body {
    height: 100%;
    overflow: auto;
  }

  .detail-inner {
    height: 100%;

    .top {
      border-bottom: 1px solid #e9e9e9;
    }

    .list-box {
      overflow: auto;
      position: absolute;
      top: 37px;
      bottom: 0;
      left: 0;
      right: 0;

      .thumbnail2 {
        position: relative;
        border: 2px solid #f3f5f7;
        border-radius: 5px;
        display: inline-block;
        overflow: hidden;
        background-color: #d3d3d3;
        cursor: pointer;

        img {
          position: absolute;
          top: 50%;
          left: 50%;
          display: block;
          min-width: 100%;
          min-height: 100%;
          transform: translate(-50%, -50%);
        }

        .v_icon,
        .GIF-IMG {
          position: absolute;
          right: 5px;
          bottom: 5px;
          font-size: 22px;
          color: #fff;
        }

        .GIF-IMG {
          font-size: 24px;
        }

        .icon {
          position: absolute;
          left: 8px;
          bottom: 10px;
          font-size: 20px;
          color: red;
        }

        &:hover {
          border-color: $bg-color;
        }

        .checkIcon {
          display: flex;
          visibility: hidden;
          -ms-flex-align: center;
          align-items: center;
          position: absolute;
          right: 5px;
          width: 20px;
          height: 20px;
          top: 5px;
          font-size: 21px;
          background: #fff;
          border-radius: 4px;
          cursor: pointer;
          color:  $bg-color;
        }

        .sign {
          position: absolute;
          top: 5px;
          left: 5px;
          height: 15px;
          color: #000;
          text-shadow: 2px 2px 2px #fff;
          border-radius: 3px;
          line-height: 15px;
          font-size: 12px;
        }

        &:hover .checkIcon {
          visibility: initial;
        }
      }

      .checkon {
        border-color: $bg-color;

        .checkIcon {
          visibility: initial;
        }
      }
    }

    .check-all {
      display: block;
      position: absolute;
      height: 17px;
      top: 10px;
      right: 10px;
      font-size: 16px;
      line-height: 17px;

      i {
        margin-right: 5px;
        cursor: pointer;
      }

      .invert-Selection {
        margin-left: 10px;
        color: $bg-color;
        cursor: pointer;
      }
    }
  }

  .albumslist {
    position: relative;
    float: left;
    width: 195px;
    margin: 5px 10px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 2px 6px 0px rgba(235, 235, 235, 1);
    border: 2px solid rgba(255, 255, 255, 1);
    border-radius: 4px;
    cursor: pointer;

    .top-img {
      height: 135px;
      background-color: #f3f5f7;
      background-repeat: no-repeat;
      background-position: center;
      margin-bottom: 8px;

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        border-radius: 4px 4px 0 0;
      }
    }

    .bottom-information {
      position: relative;
      padding: 0 5px;

      .name-time {
        .album-name {
          width: 120px;
          height: 19px;
          font-size: 14px;
          color: rgba(0, 0, 0, 1);
          line-height: 19px;
          margin: 11px 0;
        }

        .update-time {
          overflow: hidden;
          margin-bottom: 10px;

          span {
            display: block;
            float: left;
            height: 21px;
            background: rgba(243, 243, 243, 1);
            border-radius: 3px;
            font-size: 12px;
            font-weight: 400;
            color: rgba(144, 144, 144, 1);
            line-height: 21px;
            padding: 0 7px;
          }
        }

        .photo-num {
          position: absolute;
          top: 0;
          right: 5px;
          font-size: 12px;
          font-weight: 400;
          color: #909090;
        }
      }
    }
  }
  .icon-xuanzhong1{
    color: $bg-color;
  }
}
